/*-------- Vecotr Map -------*/
.jvectormap-container {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
}
.jvectormap-tip {
	position: absolute;
	display: none;
	border-radius: 5px;
	background: #cecde0;
	color: $white;
	padding: 6px;
	font-size: $default-value-size * 11;
	line-height: 1;
	font-weight: $font-weight-bold;
	small {
		font-size: inherit;
		font-weight: $font-weight-normal;
	}
}
.jvectormap-goback, .jvectormap-zoomin, .jvectormap-zoomout {
	position: absolute;
	inset-inline-start: 10px;
	border-radius: 5px;
	background: #292929;
	padding: 3px;
	color: $white;
	cursor: pointer;
	line-height: 10px;
	text-align: center;
	box-sizing: content-box;
}
.jvectormap-zoomin, .jvectormap-zoomout {
	width: 10px;
	height: 10px;
}
.jvectormap-zoomin {
	top: 10px;
}
.jvectormap-zoomout {
	top: 30px;
}
.jvectormap-goback {
	bottom: 10px;
	z-index: 1000;
	padding: 6px;
}
.jvectormap-spinner {
	position: absolute;
	inset-inline-start: 0;
	top: 0;
	inset-inline-end: 0;
	bottom: 0;
	background: center no-repeat url(data:image/gif;base64,);
}
.jvectormap-legend-title {
	font-weight: $font-weight-bold;
	font-size: $default-value-size * 14;
	text-align: center;
}
.jvectormap-legend-cnt {
	position: absolute;
}
.jvectormap-legend-cnt-h {
	bottom: 0;
	inset-inline-end: 0;
}
.jvectormap-legend-cnt-v {
	top: 0;
	inset-inline-end: 0;
}
.jvectormap-legend {
	background: $black;
	color: $white;
	border-radius: 5px;
}
.jvectormap-legend-cnt-h .jvectormap-legend {
	float: #{$float-left};
	margin: 0 10px 10px 0;
	padding: 3px 3px 1px 3px;
	.jvectormap-legend-tick {
		float: #{$float-left};
	}
}
.jvectormap-legend-cnt-v .jvectormap-legend {
	margin: 10px 10px 0 0;
	padding: 3px;
}
.jvectormap-legend-cnt-h {
	.jvectormap-legend-tick {
		width: 40px;
	}
	.jvectormap-legend-tick-sample {
		height: 15px;
	}
}
.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
	height: 20px;
	width: 20px;
	display: inline-block;
	vertical-align: middle;
}
.jvectormap-legend-tick-text {
	font-size: $default-value-size * 12;
}
.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
	text-align: center;
}
.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
	display: inline-block;
	vertical-align: middle;
	line-height: 20px;
	padding-inline-start: 3px;
}
/*-------- Vecotr Map -------*/
